import React, { Component } from "react";
import { Nav, NavItem, NavLink } from "reactstrap";
import { classnames } from "classnames";

export default class Help extends Component {
  constructor(props) {
    super(props);

    this.guides = [
      "React"
      // "Angular",
      // "HTML"
    ];
    this.layouts = [
      {
        link: "https://github.com/BHP-DevHub/BHP-react-starter-fullwidth",
        image: "full-width.png"
      },
      {
        link: "https://github.com/BHP-DevHub/BHP-react-starter-left-sidebar",
        image: "left-sidebar.png"
      },
      { link: "", image: "content-listing.png" },
      { link: "", image: "content-listing-with-right-sidebar.png" },
      { link: "", image: "right-sidebar.png" },
      { link: "", image: "search.png" },
      { link: "", image: "top-emphasis.png" },
      { link: "", image: "top-emphasis-with-right-sidebar.png" }
    ];
    this.state = {
      activeTab: this.guides[0]
    };

    this.toggleTab = this.toggleTab.bind(this);
  }

  toggleTab(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

  render() {
    const { activeTab } = this.state;
    const { guides, layouts } = this;

    return (
      <div>
        <Nav tabs>
          {guides.map((tab, index) => (
            <NavItem key={index}>
              <NavLink
                onClick={() => this.toggleTab(tab)}
                className={activeTab === tab ? "active" : null}
                href="#"
              >
                {tab}
              </NavLink>
            </NavItem>
          ))}
        </Nav>

        {/* react templates */}
        <div className="container">
          {activeTab === guides[0] && (
            <div style={{ display: "flex", flexDirection: "column" }}>
              <div style={{ flexDirection: "row" }}>
                {layouts.map(
                  (layout, index) =>
                    layout.link && (
                      <a key={index} href={layout.link}>
                        <img
                          style={{ width: "270px", padding: "10px" }}
                          src={
                            process.env.PUBLIC_URL +
                            "/assets/layouts/" +
                            layout.image
                          }
                        />
                      </a>
                    )
                )}
              </div>
            </div>
          )}

          {/* angular templates */}
          {activeTab === guides[1] && <div>{guides[1]}</div>}

          {/* html templates */}
          {activeTab === guides[2] && <div>{guides[2]}</div>}
        </div>
      </div>
    );
  }
}



